import React, { useContext, useEffect, useRef, useState } from "react";
import { Context } from "../context";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlay,
  faBackward,
  faForward,
  faPause,
} from "@fortawesome/free-solid-svg-icons";

function Player() {
  const {
    songs,
    setSongs,
    currentSong,
    setCurrentSong,
    isPlaying,
    setIsPlaying,
    audioRef,
    playSongHandler,
  } = useContext(Context);

  const [songInfo, setSongInfo] = useState({
    currentTime: 0,
    duraton: 0,
  });

  const timeUpdateHandler = (e) => {
    const currentTime = e.target.currentTime;
    const duration = e.target.duration;
    setSongInfo({ ...songInfo, currentTime, duration });
  };

  const formatTime = (time) => {
    return (
      Math.floor(time / 60) + ":" + ("0" + Math.floor(time % 60)).slice(-2)
    );
  };

  const dragHandler = (e) => {
    audioRef.current.currentTime = e.target.value;
    setSongInfo({ ...songInfo, currentTime: e.target.value });
  };

  const forward = async () => {
    const songIndex = songs.findIndex((song) => song.id === currentSong.id);
    if (songIndex < songs.length - 1) {
      await setCurrentSong(songs[songIndex + 1]);
    }
    isPlaying && audioRef.current.play();
  };

  const nextSong = async () => {
    const songIndex = songs.findIndex((song) => song.id === currentSong.id);
    if (songIndex < songs.length - 1) {
      await setCurrentSong(songs[songIndex + 1]);
    }
    isPlaying && audioRef.current.play();
  };

  useEffect(() => {
    const updatedSongs = songs.map((obj) => {
      if (obj.id == currentSong.id) {
        return {
          ...obj,
          active: true,
        };
      } else {
        return {
          ...obj,
          active: false,
        };
      }
    });
    setSongs(updatedSongs);
  }, [currentSong]);

  useEffect(() => {
    setIsPlaying(true);
  }, [currentSong]);

  const backword = async () => {
    const songIndex = songs.findIndex((song) => song.id === currentSong.id);
    if (songIndex > 0) {
      await setCurrentSong(songs[songIndex - 1]);
    }
    isPlaying && audioRef.current.play();
  };
  return (
    <div className="play">
      <div className="time-control">
        <p>{formatTime(songInfo.currentTime)}</p>
        <input
          type="range"
          min={0}
          value={songInfo.currentTime}
          max={songInfo.duration ? songInfo.duration : "0:0"}
          onChange={dragHandler}
        />
        <p>{formatTime(songInfo.duration)}</p>
      </div>

      <div className="play-control">
        <FontAwesomeIcon
          className="skip-backward"
          icon={faBackward}
          size="2x"
          onClick={backword}
        />
        <FontAwesomeIcon
          onClick={playSongHandler}
          className="play-pause"
          icon={isPlaying ? faPause : faPlay}
          size="2x"
        />
        <FontAwesomeIcon
          className="skip-forward"
          icon={faForward}
          size="2x"
          onClick={forward}
        />
      </div>
      <audio
        ref={audioRef}
        onTimeUpdate={timeUpdateHandler}
        // this make the currect  time show when the audio data is loaded without it, it will show nan until i hit play icon
        onLoadedMetadata={timeUpdateHandler}
        src={currentSong.audio}
        onEnded={nextSong}
      ></audio>
    </div>
  );
}

export default Player;
