import React, { useContext, useState } from "react";
import { Context } from "../context";
import LibrarySong from "./LibrarySong";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

function Library() {
  const { songs, toggleLibrary, setToggleLibrary } = useContext(Context);

  return (
    <div className={`library ${toggleLibrary ? "active-library" : ""}`}>
      <div className="library-nav">
        <h2 className="library-dashboard">Dashboard</h2>
        <FontAwesomeIcon
          className="library-closeIcon"
          icon={faTimes}
          size="2x"
          onClick={() => setToggleLibrary(!toggleLibrary)}
        />
      </div>

      <div className="library-content">
        {songs.map((song) => (
          <LibrarySong song={song} key={song.id} />
        ))}
      </div>
    </div>
  );
}

export default Library;
