import React, { useContext } from "react";
import { Context } from "../context";

function LibrarySong({ song }) {
  const { setCurrentSong, setIsPlaying, audioRef, isPlaying, songs, setSongs } =
    useContext(Context);

  const hanleClick = async () => {
    // to change carrent song when click it from library
    await setCurrentSong(song);
    setIsPlaying(true);

    isPlaying && audioRef.current.play();
    // update active to true to make it show as selected when i click it
    const updatedSongs = songs.map((obj) => {
      if (obj.id == song.id) {
        return {
          ...obj,
          active: true,
        };
      } else {
        return {
          ...obj,
          active: false,
        };
      }
    });
    setSongs(updatedSongs);
  };
  return (
    <div
      className={`library-song  ${song.active && `selected`} `}
      onClick={hanleClick}
    >
      <img src={song.cover} alt="song cover" />
      <div className="song-infos">
        <h2>{song.name} </h2>
        <h3>{song.artist}</h3>
      </div>
    </div>
  );
}

export default LibrarySong;
