import React, { useContext } from "react";
import { Context } from "../context";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { faMusic } from "@fortawesome/free-solid-svg-icons";
function Nav() {
  const { currentSong, toggleLibrary, setToggleLibrary } = useContext(Context);
  return (
    <div className="nav">
      <div className="nav-name">Songer ChillOut</div>
      <button
        className="nav-dashboard"
        onClick={() => setToggleLibrary(!toggleLibrary)}
      >
        Dashboard
        <FontAwesomeIcon className="music-icon" icon={faMusic} size="1x" />
      </button>
    </div>
  );
}

export default Nav;
